import React from 'react';
import quizResults from "../api/quizResults";
import PropTypes from 'prop-types'
import {FacebookShareButton,FacebookIcon, TwitterShareButton, TwitterIcon, TelegramShareButton, TelegramIcon} from 'react-share';

function Result(props) {

    const findResult =  quizResults.find(item => item.id === props.quizResult);
    const shareUrl = `${window.location}index${findResult.id}.html`;
    // localStorage.setItem('result',findResult.id);

     return (
        <div className="result">
            <div className="social-media">
                <span>Поділіться з друзями!</span>
                <FacebookShareButton url={shareUrl} >
                <FacebookIcon size={32} round={true} />
                </FacebookShareButton>
                <TwitterShareButton url={shareUrl} >
                    <TwitterIcon size={32} round={true} />
                </TwitterShareButton>
                <TelegramShareButton url={shareUrl} >
                    <TelegramIcon size={32} round={true} />
                </TelegramShareButton>
            </div>
            <h2 className="header"> {findResult.header}</h2>
            <img src={findResult.img} alt=""/>
            <div className="result__desc" dangerouslySetInnerHTML={ {__html: findResult.desc} } />
            {/*<img src={findResult.render} alt=""/>*/}
            <div className="result__desc" dangerouslySetInnerHTML={ {__html: findResult.ad} } />
            <button className="nextbutton startthegame" onClick={props.bedinAgainGame}>Спробувати ще раз
                <svg width="24" height="24" xmlns="http://www.w3.org/2000/svg"
                     >
                    <path
                        d="M21.883 12l-7.527 6.235.644.765 9-7.521-9-7.479-.645.764 7.529 6.236h-21.884v1h21.883z"></path>
                </svg>
            </button>
            {/*{findResult(props.quizResult)}*/}
            {/*{quizResults[props.quizResult].header}*/}
        </div>
    );
}

Result.propTypes =
{
    quizResult: PropTypes.string.isRequired,
}
;
export default Result;